.main-wrapper.status .status-list_wrapper,
.main-wrapper.status .status-form_wrapper {
  margin-top: 5rem; }

.main-wrapper.status .status-table {
  margin: 1rem; }

.main-wrapper.status .cards-wrapper .card {
  width: 20rem;
  text-align: center; }

.main-wrapper.status .status-table th {
  text-transform: capitalize;
  text-align: center; }

.main-wrapper.status .status-table td {
  text-align: center; }

.main-wrapper.status .card-header {
  text-align: center;
  font-size: 1.5rem;
  font-weight: bold; }

.main-wrapper.status .card-user {
  margin-top: 2rem; }

@media only screen and (max-width: 780px) {
  .main-wrapper.status .text-wrapper {
    padding: 6rem 2rem 6rem 2rem; } }
