@import url("https://fonts.googleapis.com/css2?family=Abel&family=Roboto:wght@100&display=swap");
:root {
  font-size: 16px; }

#root {
  background-color: #eee; }

html {
  background-color: #eee; }

body {
  margin: 0;
  font-family: 'Roboto', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: black;
  background-color: #ccc;
  padding: 0; }
  body::-webkit-scrollbar {
    width: 0.25rem; }
    body::-webkit-scrollbar-track {
      background: #1e1e24; }
    body::-webkit-scrollbar-thumb {
      background: #6649b8; }

.main-wrapper {
  font-family: 'Abel', sans-serif;
  font-weight: 500; }
  .main-wrapper h1 {
    text-align: center; }
  .main-wrapper .cards-wrapper .card {
    box-shadow: 0 0 20px 4px #555558; }
  .main-wrapper button.btn-primary, .main-wrapper a.btn-primary {
    background-color: #574b90;
    border: solid 1px #6649b8; }
    .main-wrapper button.btn-primary:hover, .main-wrapper a.btn-primary:hover {
      filter: grayscale(30%) opacity(0.7); }

.copy-right {
  position: fixed;
  width: 130px;
  right: 20px;
  bottom: 5px;
  text-align: center;
  padding: 6px 10px;
  border-radius: 3px;
  background: rgba(255, 255, 255, 0.1);
  font-size: 0.6rem;
  font-weight: bold;
  text-align: center;
  font-family: 'Roboto', sans-serif;
  border: 1px solid #bbb;
  color: #eee;
  text-shadow: 0px 1px 1px black; }

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace; }

@media only screen and (min-width: 600px) {
  .desktop-only {
    display: block !important; } }

@media only screen and (max-width: 780px) {
  .desktop-only {
    display: none !important; } }

@media only screen and (min-width: 600px) {
  .mobile-only {
    display: none !important; } }

@media only screen and (max-width: 780px) {
  .mobile-only {
    display: block !important; } }

.card, .table-responsive {
  box-shadow: 0 0 40px 4px #111118; }

ul {
  list-style-type: none; }

.form-custom-group {
  display: flex;
  margin-bottom: 1rem;
  width: 100%; }
  .form-custom-group .label-name {
    flex: 20% 0; }
  .form-custom-group .custom-input {
    flex: 0 60%; }

div.form-custom-group-i {
  width: 50%;
  display: block;
  margin-bottom: 1rem;
  height: 3.5rem; }
  div.form-custom-group-i .custom-input.form-control {
    width: 100%;
    height: 100%;
    padding-top: 1rem;
    border: none; }
    div.form-custom-group-i .custom-input.form-control:focus {
      z-index: 0; }
  div.form-custom-group-i .label-name {
    position: absolute;
    bottom: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: transparent;
    z-index: 2;
    margin: 0;
    pointer-events: none;
    border-bottom: 1px solid black;
    font-size: 1rem;
    font-weight: 500; }
    div.form-custom-group-i .label-name::after {
      content: '';
      position: absolute;
      left: 0;
      bottom: -1px;
      height: 100%;
      width: 100%;
      border-bottom: 2px solid #5fa8d3; }

.main-wrapper h1 {
  margin-top: 3rem; }
