.main-wrapper.profile h1 {
  text-align: center;
  margin-top: 5rem; }

.main-wrapper.profile .cards-wrapper {
  margin-top: 2rem; }

.main-wrapper.profile .card-image {
  max-width: 80px;
  margin: 0 auto; }

.main-wrapper.profile .card-icon {
  font-size: 4rem;
  margin: 0 auto; }

.main-wrapper.profile .text-wrapper {
  padding: 2rem; }

@media only screen and (max-width: 1024px) {
  .main-wrapper.profile {
    padding-left: 2rem;
    padding-right: 0;
    margin-right: 0; } }

@media only screen and (max-width: 780px) {
  .main-wrapper.profile {
    margin: 0;
    padding: 0;
    width: 100%;
    max-width: 100%; }
    .main-wrapper.profile h1 {
      width: 100%;
      background-color: #1e1e24;
      text-align: center;
      color: #b6b6b6;
      font-size: 2rem;
      padding: 1rem;
      position: fixed;
      z-index: 2;
      margin: 0; }
    .main-wrapper.profile .cards-wrapper {
      margin-top: 5rem;
      margin-bottom: 2rem; } }
