.breadcrumb-wrapper {
  margin-top: 1rem;
  border-radius: 4px;
  background-color: #23232e;
  padding: 1rem; }
  .breadcrumb-wrapper .MuiBreadcrumbs-separator {
    color: white; }
  .breadcrumb-wrapper a {
    color: white;
    cursor: pointer; }
    .breadcrumb-wrapper a:hover {
      color: white;
      text-shadow: 1px 1px 1px #ccc; }
  .breadcrumb-wrapper .current {
    font-weight: bold;
    color: #fff; }
